.about-container {
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(21, 45, 53);
  color: rgb(212, 236, 221);
}

.about-container h1 {
  margin: 40px 0px 30px 0px;
}

.about-container p {
  margin-top: 0px;
  width: 90vw;
  font-size: 1.3em;
  line-height: 1.8em;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .about-container h1 {
    margin: 40px 0px 30px 0px;
    font-size: 5em;
  }
  
  .about-container p {
    margin-top: 2%;
    width: 60vw;
    font-size: 2em;
    line-height: 2em;
  }
}